<template>
  <view class="qna-page">
    <!-- È°∂ÈÉ®Ê†áÈ¢ò -->
    <view class="header">
      <text class="title">ÁîúËúúÈóÆÁ≠î</text>
      <text class="subtitle">Áî®ÈóÆÈ¢òÂíåÁ≠îÊ°àÊõ¥‰∫ÜËß£ÂΩºÊ≠§</text>
    </view>

    <!-- ÈóÆÈ¢òÂç°Áâá -->
    <view class="question-card">
      <text class="q-text">{{ currentQuestion.text }}</text>
    </view>

    <!-- ÊàëÁöÑÂõûÁ≠îËæìÂÖ• -->
    <view class="answer-card">
      <text class="a-label">ÊàëÁöÑÂõûÁ≠î</text>
      <textarea class="a-input" v-model="myAnswer" placeholder="ËæìÂÖ•‰Ω†ÁöÑÁ≠îÊ°à..." />
      <view class="actions">
        <button class="btn primary" @click="submitAnswer">Êèê‰∫§Á≠îÊ°à</button>
        <button class="btn secondary" @click="nextQuestion">‰∏ã‰∏ÄÈ¢ò</button>
      </view>
    </view>

    <!-- ÂØπÊñπÁ≠îÊ°àÊòæÁ§∫ÔºàÊèê‰∫§ÂêéÂá∫Áé∞Ôºâ -->
    <view v-if="partnerAnswer" class="partner-card">
      <text class="p-label">TA ÁöÑÁ≠îÊ°à</text>
      <text class="p-text">{{ partnerAnswer }}</text>
    </view>

    <!-- ÊÇ¨ÊµÆËá™ÂÆö‰πâÈóÆÈ¢òÊåâÈíÆ -->
    <view class="floating custom-floating" @click="showCustomModal = true">
      <text class="float-icon">‚úö</text>
      <text class="float-text">Ëá™ÂÆö‰πâÈóÆÈ¢ò</text>
    </view>

    <!-- ÊÇ¨ÊµÆÂéÜÂè≤ÊåâÈíÆ -->
    <view class="floating" @click="openHistory">
      <text class="float-icon">üïò</text>
      <text class="float-text">ÂéÜÂè≤ÂõûÁ≠î</text>
    </view>

    <!-- ÂéÜÂè≤ËÆ∞ÂΩïÂºπÁ™ó -->
    <view v-if="showHistory" class="modal-mask" @click="closeHistory">
      <view class="modal" @click.stop>
        <text class="modal-title">ÂéÜÂè≤ÂõûÁ≠îËÆ∞ÂΩï</text>
        <scroll-view class="history-list" scroll-y="true">
          <view v-for="(item, i) in history" :key="i" class="history-item">
            <text class="h-q">Q: {{ item.question }}</text>
            <text class="h-me">Êàë: {{ item.myAnswer }}</text>
            <text class="h-ta">TA: {{ item.partnerAnswer }}</text>
            <text class="h-time">{{ item.time }}</text>
          </view>
        </scroll-view>
        <view class="modal-actions">
          <button class="btn secondary" @click="closeHistory">ÂÖ≥Èó≠</button>
          <button class="btn" @click="clearHistory">Ê∏ÖÁ©∫ËÆ∞ÂΩï</button>
        </view>
      </view>
    </view>

    <!-- Ëá™ÂÆö‰πâÈóÆÈ¢òÂºπÁ™ó -->
    <view v-if="showCustomModal" class="modal-mask" @click="closeCustomModal">
      <view class="modal custom-modal" @click.stop>
        <text class="modal-title">ÁÆ°ÁêÜÈóÆÈ¢òÂ∫ì</text>
        
        <!-- Ê∑ªÂä†Êñ∞ÈóÆÈ¢ò -->
        <view class="add-question">
          <textarea 
            class="q-input" 
            v-model="newQuestion" 
            placeholder="ËæìÂÖ•‰Ω†ÊÉ≥ÈóÆÁöÑÈóÆÈ¢ò..."
            maxlength="100"
          />
          <button class="btn primary small" @click="addCustomQuestion">Ê∑ªÂä†ÈóÆÈ¢ò</button>
        </view>

        <!-- ÈóÆÈ¢òÂàóË°® -->
        <scroll-view class="question-list" scroll-y="true">
          <view class="section-title">È¢ÑËÆæÈóÆÈ¢ò ({{ defaultQuestions.length }})</view>
          <view v-for="(q, i) in defaultQuestions" :key="'default-'+i" class="question-item">
            <text class="q-num">{{ i + 1 }}.</text>
            <text class="q-content">{{ q.text }}</text>
          </view>

          <view class="section-title" style="margin-top: 20rpx;">Ëá™ÂÆö‰πâÈóÆÈ¢ò ({{ customQuestions.length }})</view>
          <view v-for="(q, i) in customQuestions" :key="'custom-'+i" class="question-item custom">
            <text class="q-num">{{ defaultQuestions.length + i + 1 }}.</text>
            <text class="q-content">{{ q.text }}</text>
            <text class="q-delete" @click="deleteCustomQuestion(i)">Âà†Èô§</text>
          </view>
          <view v-if="customQuestions.length === 0" class="empty-hint">
            <text>ÊöÇÊó†Ëá™ÂÆö‰πâÈóÆÈ¢ò</text>
          </view>
        </scroll-view>

        <view class="modal-actions">
          <button class="btn secondary" @click="closeCustomModal">ÂÖ≥Èó≠</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { 
  getQuestions, 
  submitAnswer, 
  getHistory, 
  addCustomQuestion, 
  deleteCustomQuestion 
} from '@/api/qna.js';

export default {
  async onLoad(options) {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÂíåtoken
    const loginInfo = uni.getStorageSync('login_info');
    if (!loginInfo || !loginInfo.token) {
      uni.showModal({
        title: 'ÈúÄË¶ÅÁôªÂΩï',
        content: 'ÁîúËúúÈóÆÁ≠îÂäüËÉΩÈúÄË¶ÅÁôªÂΩïÂêéÊâçËÉΩ‰ΩøÁî®ÔºåËØ∑ÂÖàÁôªÂΩï',
        showCancel: false,
        success: () => {
          uni.reLaunch({
            url: '/pages/login/index'
          });
        }
      });
      return;
    }
    
    // ‰ªéÂêéÁ´ØÂä†ËΩΩÈóÆÈ¢òÂàóË°®ÂíåÂéÜÂè≤ËÆ∞ÂΩï
    await this.loadQuestionsFromServer();
    await this.loadHistoryFromServer();
    
    // Âä†ËΩΩÂÆåÂéÜÂè≤ÂêéÔºåÊâæÂà∞Á¨¨‰∏Ä‰∏™Êú™ÂõûÁ≠îÁöÑÈóÆÈ¢ò
    const qid = Number(options && options.qid);
    if (qid) {
      const idx = this.unansweredQuestions.findIndex(q => q.id === qid);
      if (idx >= 0) this.qIndex = idx;
    } else {
      // ÈªòËÆ§ÊòæÁ§∫Á¨¨‰∏Ä‰∏™Êú™ÂõûÁ≠îÁöÑÈóÆÈ¢ò
      this.qIndex = 0;
    }
    // Â¶ÇÊûúÊê∫Â∏¶ timeÔºåÂõûÊòæÂΩìÂ§©Á≠îÊ°à
    const time = options && options.time ? decodeURIComponent(options.time) : '';
    const rec = this.history.find(r => r.questionId === (qid || this.currentQuestion.id) && (!time || r.time === time));
    if (rec) {
      this.myAnswer = rec.myAnswer || '';
      this.partnerAnswer = rec.partnerAnswer || '';
    }
  },
  data() {
    return {
      defaultQuestions: [
        { id: 1, text: 'Êàë‰ª¨Á¨¨‰∏ÄÊ¨°Á∫¶‰ºöÁöÑÂú∞ÁÇπÊòØÂì™ÈáåÔºü', isDefault: true },
        { id: 2, text: '‰Ω†ÊúÄÂñúÊ¨¢ÊàëÂÅöÁöÑÂì™ÈÅìËèúÔºü', isDefault: true },
        { id: 3, text: 'Â¶ÇÊûúÂë®Êú´Âè™ÂÅö‰∏Ä‰ª∂‰∫ãÔºå‰Ω†Â∏åÊúõÊòØ‰ªÄ‰πàÔºü', isDefault: true },
        { id: 4, text: '‰Ω†ÂøÉ‰∏≠ÁöÑÂÆåÁæéÊóÖË°åÊòØ‰ªÄ‰πàÊ†∑ÁöÑÔºü', isDefault: true },
        { id: 5, text: 'Ëøô‰∏ÄÂπ¥ÈáåÔºå‰Ω†ÊúÄÊÑüÂä®ÁöÑ‰∏ÄÂàªÊòØ‰ªÄ‰πàÔºü', isDefault: true }
      ],
      customQuestions: [],
      qIndex: 0,
      myAnswer: '',
      partnerAnswer: '',
      showHistory: false,
      showCustomModal: false,
      newQuestion: '',
      history: []
    };
  },
  computed: {
    questions() {
      // ËøáÊª§ÊéâÊó†ÊïàÁöÑÈóÆÈ¢òÂØπË±°ÔºåÁ°Æ‰øùÊØè‰∏™ÈóÆÈ¢òÈÉΩÊúâ id Âíå text
      const validDefaultQuestions = (this.defaultQuestions || []).filter(q => q && q.id != null);
      const validCustomQuestions = (this.customQuestions || []).filter(q => q && q.id != null);
      return [...validDefaultQuestions, ...validCustomQuestions];
    },
    // ËÆ°ÁÆóÊú™ÂõûÁ≠îÁöÑÈóÆÈ¢òÂàóË°®
    unansweredQuestions() {
      // Ëé∑ÂèñÂ∑≤ÂõûÁ≠îÁöÑÈóÆÈ¢òIDÂàóË°®ÔºàÁ°Æ‰øùÁ±ªÂûã‰∏ÄËá¥Ôºâ
      const answeredIds = this.history
        .map(h => {
          // ÂÖºÂÆπ‰∏çÂêåÁöÑÂ≠óÊÆµÂêç
          const qid = h.questionId || h.question_id || h.id;
          // Áªü‰∏ÄËΩ¨Êç¢‰∏∫Êï∞Â≠óÁ±ªÂûãËøõË°åÊØîËæÉ
          return qid != null ? Number(qid) : null;
        })
        .filter(id => id != null);
      
      // ËøáÊª§Âá∫Êú™ÂõûÁ≠îÁöÑÈóÆÈ¢òÔºàÊ∑ªÂä†ÂÆâÂÖ®Ê£ÄÊü•Ôºâ
      const unanswered = this.questions.filter(q => {
        // ÂÆâÂÖ®Ê£ÄÊü•ÔºöÁ°Æ‰øù q Â≠òÂú®‰∏îÊúâ id Â±ûÊÄß
        if (!q || q.id === undefined || q.id === null) {
          console.warn('‚ö†Ô∏è ÂèëÁé∞Êó†ÊïàÁöÑÈóÆÈ¢òÂØπË±°:', q);
          return false;
        }
        const questionId = Number(q.id);
        // Ê£ÄÊü•ËΩ¨Êç¢ÂêéÁöÑIDÊòØÂê¶ÊúâÊïà
        if (isNaN(questionId)) {
          console.warn('‚ö†Ô∏è ÈóÆÈ¢òIDÊó†Êïà:', q.id);
          return false;
        }
        const isAnswered = answeredIds.includes(questionId);
        return !isAnswered && q.isActive !== false; // ËøáÊª§ÊéâÂ∑≤Á¶ÅÁî®ÁöÑÈóÆÈ¢ò
      });
      
      // ÂºÄÂèëÁéØÂ¢É‰∏ãËæìÂá∫Ë∞ÉËØï‰ø°ÊÅØ
      if (process.env.NODE_ENV === 'development') {
        console.log('üîç Êú™ÂõûÁ≠îÈóÆÈ¢òËÆ°ÁÆó:', {
          totalQuestions: this.questions.length,
          answeredIds: answeredIds,
          unansweredCount: unanswered.length,
          answeredCount: answeredIds.length,
          historyCount: this.history.length,
          questions: this.questions.map(q => ({ id: q.id, text: q.text })),
          history: this.history.map(h => ({ 
            questionId: h.questionId || h.question_id,
            question: h.question || h.questionText
          }))
        });
      }
      
      return unanswered;
    },
    currentQuestion() {
      // ‰ªéÊú™ÂõûÁ≠îÁöÑÈóÆÈ¢ò‰∏≠Ëé∑ÂèñÂΩìÂâçÈóÆÈ¢ò
      if (this.unansweredQuestions.length === 0) {
        return { id: 0, text: 'ÊâÄÊúâÈóÆÈ¢òÂ∑≤ÂõûÁ≠îÂÆåÊØïÔºÅüéâ' };
      }
      return this.unansweredQuestions[this.qIndex] || this.unansweredQuestions[0];
    }
  },
  mounted() {
    // onLoad ‰∏≠Â∑≤ÁªèÂä†ËΩΩÔºåÊó†ÈúÄÈáçÂ§çÂä†ËΩΩ
  },
  methods: {
    // ‰øùÂ≠òÂéÜÂè≤ËÆ∞ÂΩïÂà∞Êú¨Âú∞Â≠òÂÇ®
    saveHistory() {
      try {
        uni.setStorageSync('qna_history', this.history);
      } catch (e) {
        console.error('‰øùÂ≠òÂéÜÂè≤ËÆ∞ÂΩïÂ§±Ë¥•', e);
      }
    },
    async submitAnswer() {
      if (!this.myAnswer) {
        uni.showToast({ title: 'ËØ∑Â°´ÂÜô‰Ω†ÁöÑÁ≠îÊ°à', icon: 'none' });
        return;
      }
      if (this.currentQuestion.id === 0) {
        uni.showToast({ title: 'ÊâÄÊúâÈóÆÈ¢òÂ∑≤ÂõûÁ≠îÂÆåÊØï', icon: 'none' });
        return;
      }
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÂõûÁ≠îËøáËøô‰∏™ÈóÆÈ¢ò
      const alreadyAnswered = this.history.some(h => h.questionId === this.currentQuestion.id);
      if (alreadyAnswered) {
        uni.showToast({ title: 'ËØ•ÈóÆÈ¢òÂ∑≤ÁªèÂõûÁ≠îËøá‰∫Ü', icon: 'none' });
        return;
      }
      
      try {
        uni.showLoading({ title: 'Êèê‰∫§‰∏≠...' });
        
        // Ë∞ÉÁî®ÂêéÁ´ØAPIÊèê‰∫§Á≠îÊ°à
        const res = await submitAnswer({
          questionId: this.currentQuestion.id,
          answer: this.myAnswer,
          questionText: this.currentQuestion.text
        });
        
        console.log('üì• Êèê‰∫§Á≠îÊ°àÂìçÂ∫î:', res);
        
        if (res && res.success) {
          // ÂÖºÂÆπ‰∏çÂêåÁöÑÂìçÂ∫îÊ†ºÂºè
          const responseData = res.data || res;
          
          // Â¶ÇÊûúÂØπÊñπÂ∑≤ÂõûÁ≠îÔºåÊòæÁ§∫ÂØπÊñπÁ≠îÊ°àÔºàÂÖºÂÆπ hasPartnerAnswer Âíå hasPartnerAnswered ‰∏§ÁßçÂ≠óÊÆµÂêçÔºâ
          if (responseData && (responseData.hasPartnerAnswered || responseData.hasPartnerAnswer)) {
            this.partnerAnswer = responseData.partnerAnswer || '';
          }
          
          // Ê∑ªÂä†Âà∞Êú¨Âú∞ÂéÜÂè≤ËÆ∞ÂΩï
          const record = {
            id: responseData?.answerId || res?.answerId || responseData?.id || Date.now(),
            questionId: this.currentQuestion.id,
            question: this.currentQuestion.text,
            myAnswer: this.myAnswer,
            partnerAnswer: responseData?.partnerAnswer || '',
            time: new Date().toLocaleString(),
            createdAt: new Date().toISOString()
          };
          this.history.unshift(record);
          this.saveHistory();
          
          uni.showToast({ title: 'Êèê‰∫§ÊàêÂäü', icon: 'success' });
          
          // Êèê‰∫§ÂêéËá™Âä®Ë∑≥Âà∞‰∏ã‰∏ÄÈ¢ò
          setTimeout(() => {
            this.nextQuestion();
          }, 1500);
        } else {
          // Âç≥‰ΩøÂìçÂ∫îÊ†ºÂºè‰∏çÁ¨¶ÂêàÈ¢ÑÊúüÔºå‰πü‰øùÂ≠òÂà∞Êú¨Âú∞
          console.warn('‚ö†Ô∏è ÂìçÂ∫îÊ†ºÂºè‰∏çÁ¨¶ÂêàÈ¢ÑÊúü:', res);
          const record = {
            id: Date.now(),
            questionId: this.currentQuestion.id,
            question: this.currentQuestion.text,
            myAnswer: this.myAnswer,
            partnerAnswer: '',
            time: new Date().toLocaleString(),
            createdAt: new Date().toISOString()
          };
          this.history.unshift(record);
          this.saveHistory();
          uni.showToast({ title: 'Êèê‰∫§ÊàêÂäüÔºàÂ∑≤‰øùÂ≠òÂà∞Êú¨Âú∞Ôºâ', icon: 'success' });
          
          setTimeout(() => {
            this.nextQuestion();
          }, 1500);
        }
      } catch (e) {
        console.error('Êèê‰∫§Á≠îÊ°àÂ§±Ë¥•', e);
        console.error('ÈîôËØØËØ¶ÊÉÖ:', {
          statusCode: e.statusCode,
          message: e.message,
          data: e.data,
          url: e.url || 'Êú™Áü•'
        });
        
        // 401ÈîôËØØÁâπÊÆäÂ§ÑÁêÜ
        if (e.statusCode === 401) {
          uni.hideLoading();
          uni.showModal({
            title: 'ÁôªÂΩïÂ∑≤ËøáÊúü',
            content: 'ÊÇ®ÁöÑÁôªÂΩïÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï',
            showCancel: false,
            success: () => {
              uni.reLaunch({
                url: '/pages/login/index'
              });
            }
          });
          return;
        }
        
        // 404ÈîôËØØÔºöÂêéÁ´ØÊé•Âè£Êú™ÂÆûÁé∞
        if (e.statusCode === 404) {
          uni.hideLoading();
          console.warn('‚ö†Ô∏è ÂêéÁ´ØÊé•Âè£Êú™ÂÆûÁé∞: POST /api/qna/answer/submit');
          console.warn('üí° ÊèêÁ§∫: ËØ∑ËÅîÁ≥ªÂêéÁ´ØÂºÄÂèë‰∫∫ÂëòÂÆûÁé∞ËØ•Êé•Âè£ÔºåÊàñÊ£ÄÊü•Êé•Âè£Ë∑ØÂæÑÊòØÂê¶Ê≠£Á°Æ');
          
          // ‰∏¥Êó∂ÊñπÊ°àÔºö‰øùÂ≠òÂà∞Êú¨Âú∞ÔºåÁ≠âÂêéÁ´ØÊé•Âè£Â∞±Áª™ÂêéÂÜçÂêåÊ≠•
          uni.showModal({
            title: 'Êé•Âè£Êú™ÂÆûÁé∞',
            content: 'Êèê‰∫§Á≠îÊ°àÊé•Âè£ÊöÇÊú™ÂÆûÁé∞ÔºåÂ∑≤‰øùÂ≠òÂà∞Êú¨Âú∞„ÄÇËØ∑ËÅîÁ≥ªÂêéÁ´ØÂºÄÂèë‰∫∫ÂëòÂÆûÁé∞Êé•Âè£ÔºöPOST /api/qna/answer/submit',
            showCancel: false,
            confirmText: 'Áü•ÈÅì‰∫Ü',
            success: () => {
              // Êú¨Âú∞‰øùÂ≠òÁ≠îÊ°àËÆ∞ÂΩïÔºà‰∏¥Êó∂ÊñπÊ°àÔºâ
              const record = {
                id: Date.now(), // ‰∏¥Êó∂ID
                questionId: this.currentQuestion.id,
                question: this.currentQuestion.text,
                myAnswer: this.myAnswer,
                partnerAnswer: '',
                time: new Date().toLocaleString(),
                createdAt: new Date().toISOString(),
                _pendingSync: true // Ê†áËÆ∞‰∏∫ÂæÖÂêåÊ≠•
              };
              this.history.unshift(record);
              this.saveHistory();
              uni.showToast({ title: 'Â∑≤‰øùÂ≠òÂà∞Êú¨Âú∞', icon: 'none' });
              
              // Êèê‰∫§ÂêéËá™Âä®Ë∑≥Âà∞‰∏ã‰∏ÄÈ¢ò
              setTimeout(() => {
                this.nextQuestion();
              }, 1500);
            }
          });
          return;
        }
        
        uni.hideLoading();
        uni.showToast({ 
          title: `Êèê‰∫§Â§±Ë¥•: ${e.statusCode || 'ÁΩëÁªúÈîôËØØ'}`, 
          icon: 'none',
          duration: 3000
        });
      } finally {
        // Á°Æ‰øùloadingÂÖ≥Èó≠
        uni.hideLoading();
      }
    },
    nextQuestion() {
      this.partnerAnswer = '';
      this.myAnswer = '';
      // ÈáçÊñ∞ËÆ°ÁÆóÊú™ÂõûÁ≠îÈóÆÈ¢òÂàóË°®ÔºåÊòæÁ§∫‰∏ã‰∏Ä‰∏™
      if (this.qIndex < this.unansweredQuestions.length - 1) {
        this.qIndex += 1;
      } else {
        this.qIndex = 0; // ÂõûÂà∞Á¨¨‰∏Ä‰∏™Êú™ÂõûÁ≠îÁöÑ
      }
      if (this.unansweredQuestions.length === 0) {
        uni.showToast({ title: 'ÊâÄÊúâÈóÆÈ¢òÂ∑≤ÂõûÁ≠îÂÆåÊØïÔºÅ', icon: 'success' });
      }
    },
    openHistory() {
      
      uni.navigateTo({ url: '/pages/qna/history' });
    },
    closeHistory() {
      this.showHistory = false;
    },
    clearHistory() {
      this.history = [];
      this.saveHistory();
      uni.showToast({ title: 'ËÆ∞ÂΩïÂ∑≤Ê∏ÖÁ©∫', icon: 'none' });
    },
    // ‰ªéÂêéÁ´ØÂä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩï
    async loadHistoryFromServer() {
      try {
        const res = await getHistory({ page: 1, pageSize: 100 });
        console.log('üì• ÂéÜÂè≤ËÆ∞ÂΩïÂìçÂ∫î:', res);
        
        let historyList = [];
        
        // Â§ÑÁêÜ‰∏çÂêåÁöÑÂìçÂ∫îÊ†ºÂºèÔºàÊåâ‰ºòÂÖàÁ∫ßÈ°∫Â∫èÔºâ
        if (res && res.success && Array.isArray(res.history)) {
          // Ê†ºÂºè: { success: true, history: [...], message: "Ëé∑ÂèñÊàêÂäü", totalCount: 5 }
          historyList = res.history;
        } else if (res && res.success && Array.isArray(res.answers)) {
          // Ê†ºÂºè: { success: true, answers: [...], message: "Ëé∑ÂèñÊàêÂäü", totalCount: 0 }
          historyList = res.answers;
        } else if (res && res.success && res.data && res.data.list) {
          // Ê†ºÂºè: { success: true, data: { list: [...] } }
          historyList = Array.isArray(res.data.list) ? res.data.list : [];
        } else if (res && res.success && res.data && Array.isArray(res.data)) {
          // Ê†ºÂºè: { success: true, data: [...] } ÔºàÁõ¥Êé•ÊòØÊï∞ÁªÑÔºâ
          historyList = res.data;
        } else if (res && res.list) {
          // Ê†ºÂºè: { list: [...] } ÔºàÁõ¥Êé•ËøîÂõûÊï∞ÊçÆÔºâ
          historyList = Array.isArray(res.list) ? res.list : [];
        } else if (Array.isArray(res)) {
          // Ê†ºÂºè: [...] ÔºàÁõ¥Êé•ËøîÂõûÊï∞ÁªÑÔºâ
          historyList = res;
        } else {
          console.warn('‚ö†Ô∏è ÂéÜÂè≤ËÆ∞ÂΩïÂìçÂ∫îÊ†ºÂºè‰∏çÁ¨¶ÂêàÈ¢ÑÊúü:', res);
          historyList = [];
        }
        
        // Ê†áÂáÜÂåñÂéÜÂè≤ËÆ∞ÂΩïÊ†ºÂºèÔºåÁ°Æ‰øùÂ≠óÊÆµÂêç‰∏ÄËá¥
        this.history = historyList.map(item => {
          // ÂÖºÂÆπÂ§öÁßçÂ≠óÊÆµÂêçÂíåÊ†ºÂºè
          const id = item.id || item.answerId;
          const questionId = item.questionId || item.question_id;
          
          // ‰ºòÂÖà‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑ questionÔºåÂ¶ÇÊûúÊ≤°ÊúâÂàô‰ªéÈóÆÈ¢òÂàóË°®‰∏≠Êü•Êâæ
          let question = item.question || item.questionText || item.question_text;
          if (!question && questionId != null) {
            // ‰ªéÈóÆÈ¢òÂàóË°®‰∏≠Ê†πÊçÆ questionId Êü•ÊâæÂØπÂ∫îÁöÑ question ÊñáÊú¨
            const allQuestions = [...(this.defaultQuestions || []), ...(this.customQuestions || [])];
            const foundQuestion = allQuestions.find(q => q && q.id != null && Number(q.id) === Number(questionId));
            if (foundQuestion && foundQuestion.text) {
              question = foundQuestion.text;
            }
          }
          
          // ÂÖºÂÆπ answer„ÄÅmyAnswer„ÄÅmy_answer Á≠âÂ§öÁßçÂ≠óÊÆµÂêç
          const myAnswer = item.myAnswer || item.answer || item.my_answer;
          const partnerAnswer = item.partnerAnswer || item.partner_answer || '';
          // ÂÖºÂÆπÂ§öÁßçÊó∂Èó¥Â≠óÊÆµÔºöansweredAt„ÄÅcreatedAt„ÄÅcreated_at„ÄÅtime„ÄÅupdatedAt
          const time = item.time || item.answeredAt || item.createdAt || item.created_at || item.updatedAt || new Date().toLocaleString();
          const createdAt = item.createdAt || item.created_at || item.answeredAt || item.updatedAt || new Date().toISOString();
          
          return {
            id,
            questionId,
            question: question || `ÈóÆÈ¢òID: ${questionId}`, // Â¶ÇÊûú‰ªçÁÑ∂Êâæ‰∏çÂà∞ÔºåÊòæÁ§∫ID‰Ωú‰∏∫Â§áÁî®
            myAnswer,
            partnerAnswer,
            time,
            createdAt,
            // ‰øùÁïôÂéüÂßãÊï∞ÊçÆ‰∏≠ÁöÑÂÖ∂‰ªñÂ≠óÊÆµÔºàÂ¶Ç questionCategory„ÄÅansweredAt Á≠âÔºâ
            questionCategory: item.questionCategory || item.category,
            answeredAt: item.answeredAt,
            updatedAt: item.updatedAt,
            ...item
          };
        });
        
        console.log('‚úÖ ÂéÜÂè≤ËÆ∞ÂΩïÂä†ËΩΩÊàêÂäü:', {
          count: this.history.length,
          totalCount: res?.totalCount,
          sample: this.history.slice(0, 3)
        });
      } catch (e) {
        console.error('Âä†ËΩΩÂéÜÂè≤ËÆ∞ÂΩïÂ§±Ë¥•', e);
        console.error('ÈîôËØØËØ¶ÊÉÖ:', {
          message: e.message,
          statusCode: e.statusCode,
          data: e.data
        });
        
        // 401ÈîôËØØÁâπÊÆäÂ§ÑÁêÜÔºà‰ΩÜ‰∏çÂºπÂá∫ÊèêÁ§∫ÔºåÂõ†‰∏∫Â∑≤ÁªèÂú®‰∏äÈù¢ÁöÑÂä†ËΩΩÈóÆÈ¢ò‰∏≠Â§ÑÁêÜ‰∫ÜÔºâ
        if (e.statusCode === 401) {
          return;
        }
        
        // Â¶ÇÊûúÂêéÁ´ØËØ∑Ê±ÇÂ§±Ë¥•ÔºåÂ∞ùËØï‰ªéÊú¨Âú∞Â≠òÂÇ®Âä†ËΩΩ
        try {
          const data = uni.getStorageSync('qna_history');
          this.history = Array.isArray(data) ? data : [];
        } catch (e2) { 
          this.history = []; 
        }
      }
    },
    // ‰ªéÂêéÁ´ØÂä†ËΩΩÈóÆÈ¢òÂàóË°®
    async loadQuestionsFromServer() {
      try {
        uni.showLoading({ title: 'Âä†ËΩΩ‰∏≠...' });
        const res = await getQuestions();
        console.log('üì• ÈóÆÈ¢òÂàóË°®ÂìçÂ∫î:', res);
        
        // Â§ÑÁêÜÂêéÁ´ØËøîÂõûÁöÑÊ†ºÂºè: { success: true, questions: Array, message: "Ëé∑ÂèñÊàêÂäü" }
        if (res && res.success && Array.isArray(res.questions)) {
          // Â∞ÜÈóÆÈ¢òÊåâ category ÂàÜÁ±ªÔºåÂπ∂ËΩ¨Êç¢Â≠óÊÆµÂêç
          const presetQuestions = [];
          const customQuestions = [];
          
          res.questions.forEach(q => {
            // Ë∑≥ËøáÊó†ÊïàÁöÑÈóÆÈ¢òÂØπË±°
            if (!q || q.id === undefined || q.id === null) {
              console.warn('‚ö†Ô∏è Ë∑≥ËøáÊó†ÊïàÁöÑÈóÆÈ¢òÂØπË±°:', q);
              return;
            }
            
            // ËΩ¨Êç¢Â≠óÊÆµÔºöquestionText -> textÔºå‰øùÁïôÂÖ∂‰ªñÂ≠óÊÆµ
            const question = {
              id: q.id,
              text: q.questionText || q.text || '', // ÂÖºÂÆπ‰∏§ÁßçÂ≠óÊÆµÂêçÔºåÁ°Æ‰øùÊúâÈªòËÆ§ÂÄº
              category: q.category || 'preset',
              isActive: q.isActive !== false, // ÈªòËÆ§‰∏∫ true
              orderIndex: q.orderIndex ?? 999,
              createdBy: q.createdBy,
              // ‰øùÁïôÂÖ∂‰ªñÂèØËÉΩÂ≠òÂú®ÁöÑÂ≠óÊÆµ
              ...q
            };
            
            // ÁßªÈô§ÂéüÂßãÁöÑ questionTextÔºåÈÅøÂÖçÊ∑∑‰π±
            if (question.questionText) {
              delete question.questionText;
            }
            
            // Êåâ category ÂàÜÁ±ª
            if (q.category === 'preset') {
              presetQuestions.push(question);
            } else if (q.category === 'custom') {
              customQuestions.push(question);
            }
          });
          
          // È¢ÑËÆæÈóÆÈ¢òÊåâ orderIndex ÊéíÂ∫è
          presetQuestions.sort((a, b) => {
            const orderA = a.orderIndex ?? 999;
            const orderB = b.orderIndex ?? 999;
            return orderA - orderB;
          });
          
          this.defaultQuestions = presetQuestions;
          this.customQuestions = customQuestions;
          
          console.log('‚úÖ ÈóÆÈ¢òÂàóË°®Âä†ËΩΩÊàêÂäü:', {
            preset: presetQuestions.length,
            custom: customQuestions.length,
            total: presetQuestions.length + customQuestions.length
          });
        } else if (res && res.success && res.data) {
          // ÂÖºÂÆπÊóßÊ†ºÂºè: { success: true, data: { defaultQuestions: [...], customQuestions: [...] } }
          this.defaultQuestions = Array.isArray(res.data.defaultQuestions) 
            ? res.data.defaultQuestions
                .filter(q => q && q.id != null) // ËøáÊª§Êó†ÊïàÊï∞ÊçÆ
                .map(q => ({
                  id: q.id,
                  text: q.questionText || q.text || '',
                  ...q
                }))
            : [];
          this.customQuestions = Array.isArray(res.data.customQuestions) 
            ? res.data.customQuestions
                .filter(q => q && q.id != null) // ËøáÊª§Êó†ÊïàÊï∞ÊçÆ
                .map(q => ({
                  id: q.id,
                  text: q.questionText || q.text || '',
                  ...q
                }))
            : [];
        } else {
          console.warn('‚ö†Ô∏è ÈóÆÈ¢òÂàóË°®ÂìçÂ∫îÊ†ºÂºè‰∏çÁ¨¶ÂêàÈ¢ÑÊúü:', res);
          this.defaultQuestions = [];
          this.customQuestions = [];
        }
      } catch (e) {
        console.error('Âä†ËΩΩÈóÆÈ¢òÂ§±Ë¥•', e);
        console.error('ÈîôËØØËØ¶ÊÉÖ:', {
          message: e.message,
          statusCode: e.statusCode,
          data: e.data
        });
        
        // 401ÈîôËØØÁâπÊÆäÂ§ÑÁêÜ
        if (e.statusCode === 401) {
          uni.hideLoading();
          uni.showModal({
            title: 'ÁôªÂΩïÂ∑≤ËøáÊúü',
            content: 'ÊÇ®ÁöÑÁôªÂΩïÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï',
            showCancel: false,
            success: () => {
              uni.reLaunch({
                url: '/pages/login/index'
              });
            }
          });
          return;
        }
        
        // Â¶ÇÊûúÂêéÁ´ØËØ∑Ê±ÇÂ§±Ë¥•Ôºå‰ΩøÁî®È¢ÑËÆæÈóÆÈ¢òÂíåÊú¨Âú∞Ëá™ÂÆö‰πâÈóÆÈ¢ò
        uni.showToast({ title: 'Âä†ËΩΩÈóÆÈ¢òÂ§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞Êï∞ÊçÆ', icon: 'none' });
        try {
          const data = uni.getStorageSync('qna_custom_questions');
          this.customQuestions = Array.isArray(data) ? data : [];
        } catch (e2) { 
          this.customQuestions = []; 
        }
      } finally {
        uni.hideLoading();
      }
    },

    // Ê∑ªÂä†Ëá™ÂÆö‰πâÈóÆÈ¢ò
    async addCustomQuestion() {
      if (!this.newQuestion || !this.newQuestion.trim()) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÈóÆÈ¢òÂÜÖÂÆπ', icon: 'none' });
        return;
      }
      
      try {
        uni.showLoading({ title: 'Ê∑ªÂä†‰∏≠...' });
        
        // Ë∞ÉÁî®ÂêéÁ´ØAPIÊ∑ªÂä†ÈóÆÈ¢ò
        const res = await addCustomQuestion(this.newQuestion.trim());
        
        if (res.success) {
          // Ê†ºÂºèÂåñÊñ∞ÈóÆÈ¢òÔºåÁ°Æ‰øùÂåÖÂê´ÊâÄÊúâÂøÖÈúÄÂ≠óÊÆµ
          const newQuestionData = res.data || {};
          const formattedQuestion = {
            id: newQuestionData.id,
            text: newQuestionData.text || newQuestionData.questionText || this.newQuestion.trim(),
            category: 'custom',
            isActive: true,
            orderIndex: 999,
            createdBy: newQuestionData.userId || newQuestionData.createdBy,
            createdAt: newQuestionData.createdAt,
            // ‰øùÁïôÂÖ∂‰ªñÂ≠óÊÆµ
            ...newQuestionData
          };
          
          // Â∞ÜÊñ∞ÈóÆÈ¢òÊ∑ªÂä†Âà∞ÂàóË°®
          this.customQuestions.push(formattedQuestion);
          this.newQuestion = '';
          
          // ÂÖ≥Èó≠ÂºπÁ™óÔºåËÆ©Áî®Êà∑ÁúãÂà∞‰∏ªÈ°µÈù¢
          this.showCustomModal = false;
          
          // Ëá™Âä®ÂàáÊç¢Âà∞Êñ∞Ê∑ªÂä†ÁöÑÈóÆÈ¢òÔºàÂ¶ÇÊûúÂÆÉÊòØÊú™ÂõûÁ≠îÁöÑÈóÆÈ¢òÔºâ
          // ‰ΩøÁî® setTimeout Á°Æ‰øùÂìçÂ∫îÂºèÊõ¥Êñ∞Â∑≤ÂÆåÊàêÔºàuni-app ‰∏≠‰ΩøÁî® setTimeout Êõ¥ÂèØÈù†Ôºâ
          setTimeout(() => {
            const newQuestionIndex = this.unansweredQuestions.findIndex(
              q => q.id === formattedQuestion.id
            );
            if (newQuestionIndex >= 0) {
              // ÊâæÂà∞Êñ∞ÈóÆÈ¢òÂú®Êú™ÂõûÁ≠îÂàóË°®‰∏≠ÁöÑ‰ΩçÁΩÆÔºåÂàáÊç¢ËøáÂéª
              this.qIndex = newQuestionIndex;
              // Ê∏ÖÁ©∫ÂΩìÂâçÁöÑÁ≠îÊ°àËæìÂÖ•ÔºåÂáÜÂ§áÂõûÁ≠îÊñ∞ÈóÆÈ¢ò
              this.myAnswer = '';
              this.partnerAnswer = '';
              
              console.log('‚úÖ Â∑≤ÂàáÊç¢Âà∞Êñ∞Ê∑ªÂä†ÁöÑÈóÆÈ¢ò:', formattedQuestion);
            }
          }, 100);
          
          uni.showToast({ title: 'ÈóÆÈ¢òÊ∑ªÂä†ÊàêÂäü', icon: 'success' });
        }
      } catch (e) {
        console.error('Ê∑ªÂä†ÈóÆÈ¢òÂ§±Ë¥•', e);
        
        // 401ÈîôËØØÁâπÊÆäÂ§ÑÁêÜ
        if (e.statusCode === 401) {
          uni.showModal({
            title: 'ÁôªÂΩïÂ∑≤ËøáÊúü',
            content: 'ÊÇ®ÁöÑÁôªÂΩïÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï',
            showCancel: false,
            success: () => {
              uni.reLaunch({
                url: '/pages/login/index'
              });
            }
          });
          return;
        }
        
        uni.showToast({ title: 'Ê∑ªÂä†Â§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
      } finally {
        uni.hideLoading();
      }
    },

    async deleteCustomQuestion(index) {
      const question = this.customQuestions[index];
      
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ÈóÆÈ¢òÂêóÔºü',
        success: async (modalRes) => {
          if (modalRes.confirm) {
            try {
              uni.showLoading({ title: 'Âà†Èô§‰∏≠...' });
              
              // Ë∞ÉÁî®ÂêéÁ´ØAPIÂà†Èô§ÈóÆÈ¢ò
              const res = await deleteCustomQuestion(question.id);
              
              if (res.success) {
                // ‰ªéÂàóË°®‰∏≠ÁßªÈô§
                this.customQuestions.splice(index, 1);
                uni.showToast({ title: 'Â∑≤Âà†Èô§', icon: 'success' });
              }
            } catch (e) {
              console.error('Âà†Èô§ÈóÆÈ¢òÂ§±Ë¥•', e);
              
              // 401ÈîôËØØÁâπÊÆäÂ§ÑÁêÜ
              if (e.statusCode === 401) {
                uni.showModal({
                  title: 'ÁôªÂΩïÂ∑≤ËøáÊúü',
                  content: 'ÊÇ®ÁöÑÁôªÂΩïÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï',
                  showCancel: false,
                  success: () => {
                    uni.reLaunch({
                      url: '/pages/login/index'
                    });
                  }
                });
                return;
              }
              
              uni.showToast({ title: 'Âà†Èô§Â§±Ë¥•ÔºåËØ∑ÈáçËØï', icon: 'none' });
            } finally {
              uni.hideLoading();
            }
          }
        }
      });
    },

    closeCustomModal() {
      this.showCustomModal = false;
      this.newQuestion = '';
    }
  }
};
</script>

